___CSS_LOADER_EXPORT___.push([module.id, `#list-display{
  width: 100%;
  display: grid;
  justify-items: start;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr 50px;
}

#list-display h2 {
  width: 100%;
  padding-bottom: 15px;
}

#list-display h2 input {
  width: 100%;
  font-size: 3rem;
  background-color: rgba(255, 255, 255, 0);
  border: none;
  word-spacing: -10px;
  color: white;
}

#tasks-list{
  width: 100%;
  overflow: scroll;
  display: grid;
  grid-auto-rows: 40px;
  grid-template-columns: 1fr;
  list-style-type: none;
  gap: 5px;
  text-align: start;
  user-select: none;
}

#tasks-list li{
  background-color: rgb(255, 255, 255);
  border-radius: 3px;
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: center;
  gap: 10px;
  padding-left: 10px;
}

#tasks-list button {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 1px solid black;
}

#new-task-entry {
  width: 100%;
  display: grid;
  grid-template-columns: 40px 1fr;
  grid-template-rows: 40px;
  align-self: end;
  gap: 5px;
  background-color: rgb(237, 238, 238);
  border-radius: 3px;
}

#new-task-entry button {
  border: none;
  border-radius: 3px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
}

#new-task-entry input {
  padding-left: 5px;
  border: none;
  border-radius: 3px;
  background-color: rgba(255, 255, 255, 0);
}

#new-task-entry input:focus {
  outline: none;
  background-color: white;
