___CSS_LOADER_EXPORT___.push([module.id, `#list-sidebar {
  user-select: none;
  
  display: grid;
  justify-items: start;
  grid-template-columns: 1fr;
  grid-template-rows: 80px 1fr 40px;
  background-color: rgb(255, 248, 240);
  color: black;
}

#list-sidebar h2 {
  font-size: 2rem;
  align-self: center;
}

#lists-list {
  width: 100%;
  font-size: 1.2rem;
  text-align: start;

  display: grid;
  grid-auto-rows: 35px;
  gap: 5px;

  justify-items: start;
}

#lists-list li {
  width: 100%;
  border: 1px black solid;
  padding: 3px;

  display: grid;
  grid-template-columns: 1fr 30px;
  align-items: center;
}

#lists-list li:hover {
  background-color: rgba(0, 0, 0, 0.137);
}

#lists-list li button {
  border-radius: 50%;
  height: 16px;
  width: 16px;
  font-size: 0.6rem;
  place-self: center;
}

#add-list-button {
  border-radius: 5px;
  border: none;
  font-size: 1.05rem;
  width: 100%;
}`, "",{"version":3,"sources":["webpack://./src/style/list-sidebar.css"],"names":[],"mappings":"AAAA;EACE,iBAAiB;;EAEjB,aAAa;EACb,oBAAoB;EACpB,0BAA0B;EAC1B,iCAAiC;EACjC,oCAAoC;EACpC,YAAY;AACd;;AAEA;EACE,eAAe;EACf,kBAAkB;AACpB;;AAEA;EACE,WAAW;EACX,iBAAiB;EACjB,iBAAiB;;EAEjB,aAAa;EACb,oBAAoB;EACpB,QAAQ;;EAER,oBAAoB;AACtB;;AAEA;EACE,WAAW;EACX,uBAAuB;EACvB,YAAY;;EAEZ,aAAa;EACb,+BAA+B;EAC/B,mBAAmB;AACrB;;AAEA;EACE,sCAAsC;AACxC;;AAEA;EACE,kBAAkB;EAClB,YAAY;EACZ,WAAW;EACX,iBAAiB;EACjB,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;EAClB,YAAY;EACZ,kBAAkB;EAClB,WAAW;AACb","sourcesContent":["#list-sidebar {\n  user-select: none;\n  \n  display: grid;\n  justify-items: start;\n  grid-template-columns: 1fr;\n  grid-template-rows: 80px 1fr 40px;\n  background-color: rgb(255, 248, 240);\n  color: black;\n}\n\n#list-sidebar h2 {\n  font-size: 2rem;\n  align-self: center;\n}\n\n#lists-list {\n  width: 100%;\n  font-size: 1.2rem;\n  text-align: start;\n\n  display: grid;\n  grid-auto-rows: 35px;\n  gap: 5px;\n\n  justify-items: start;\n}\n\n#lists-list li {\n  width: 100%;\n  border: 1px black solid;\n  padding: 3px;\n\n  display: grid;\n  grid-template-columns: 1fr 30px;\n  align-items: center;\n}\n\n#lists-list li:hover {\n  background-color: rgba(0, 0, 0, 0.137);\n}\n\n#lists-list li button {\n  border-radius: 50%;\n  height: 16px;\n  width: 16px;\n  font-size: 0.6rem;\n  place-self: center;\n}\n\n#add-list-button {\n  border-radius: 5px;\n  border: none;\n  font-size: 1.05rem;\n  width: 100%;\n}"],"sourceRoot":""}]);
