___CSS_LOADER_EXPORT___.push([module.id, `#task-options {
  width: 100%;
  background-color: white;
  display: grid;
  gap: 50px;
  grid-template-columns: 1fr;
  align-items: center;
  align-content: start;
}

#task-options header {
  border: 1px solid black;

  display: grid;
  grid-template-columns: 1fr 40px;
  grid-template-rows: 50px;
  gap: 15px;
  margin-top: 40px;
}

#task-options header input {
  width: 100%;
  border: none;
  padding-left: 10px;
}

#task-options header button {
  height: 30px;
  width: 30px;
  place-self: center;
  background-color: white;
  border: none;
  font-size: auto;
  margin-top: -5px;
  font-weight: bold;
}

#task-options div {
  display: grid;
  grid-template-columns: auto 1fr;
  font-size: 1.1rem;
  align-items: center;
  gap: 10px;
}

#task-options label {
  font-weight: bold;
}

#task-options input {
  border: 1px solid black;
}

#task-options input:focus {
  outline: none;
}

#task-options button {
  font-size: 1.5rem;
}

.task-urgent {
  border: 3px solid red;
  background-color: rgb(0, 0, 0);
  color: purple;
  font-weight: bold;
}

#task-options textarea {
  height: 250px;
  font-size: 1.2rem;
  resize: vertical;
}`, "",{"version":3,"sources":["webpack://./src/style/task-options.css"],"names":[],"mappings":"AAAA;EACE,WAAW;EACX,uBAAuB;EACvB,aAAa;EACb,SAAS;EACT,0BAA0B;EAC1B,mBAAmB;EACnB,oBAAoB;AACtB;;AAEA;EACE,uBAAuB;;EAEvB,aAAa;EACb,+BAA+B;EAC/B,wBAAwB;EACxB,SAAS;EACT,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,kBAAkB;AACpB;;AAEA;EACE,YAAY;EACZ,WAAW;EACX,kBAAkB;EAClB,uBAAuB;EACvB,YAAY;EACZ,eAAe;EACf,gBAAgB;EAChB,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,+BAA+B;EAC/B,iBAAiB;EACjB,mBAAmB;EACnB,SAAS;AACX;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,uBAAuB;AACzB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,qBAAqB;EACrB,8BAA8B;EAC9B,aAAa;EACb,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,iBAAiB;EACjB,gBAAgB;AAClB","sourcesContent":["#task-options {\n  width: 100%;\n  background-color: white;\n  display: grid;\n  gap: 50px;\n  grid-template-columns: 1fr;\n  align-items: center;\n  align-content: start;\n}\n\n#task-options header {\n  border: 1px solid black;\n\n  display: grid;\n  grid-template-columns: 1fr 40px;\n  grid-template-rows: 50px;\n  gap: 15px;\n  margin-top: 40px;\n}\n\n#task-options header input {\n  width: 100%;\n  border: none;\n  padding-left: 10px;\n}\n\n#task-options header button {\n  height: 30px;\n  width: 30px;\n  place-self: center;\n  background-color: white;\n  border: none;\n  font-size: auto;\n  margin-top: -5px;\n  font-weight: bold;\n}\n\n#task-options div {\n  display: grid;\n  grid-template-columns: auto 1fr;\n  font-size: 1.1rem;\n  align-items: center;\n  gap: 10px;\n}\n\n#task-options label {\n  font-weight: bold;\n}\n\n#task-options input {\n  border: 1px solid black;\n}\n\n#task-options input:focus {\n  outline: none;\n}\n\n#task-options button {\n  font-size: 1.5rem;\n}\n\n.task-urgent {\n  border: 3px solid red;\n  background-color: rgb(0, 0, 0);\n  color: purple;\n  font-weight: bold;\n}\n\n#task-options textarea {\n  height: 250px;\n  font-size: 1.2rem;\n  resize: vertical;\n}"],"sourceRoot":""}]);
